<template>
  <header class="section">
    <div class="container">
      <b-navbar :transparent=true type="is-black" :is-active.sync="navbarIsOpen">
        <template slot="brand">
          <b-navbar-item>
            <figure class="image is-32x32 is-square">
              <img style="min-height: 32px;" src="@/assets/Cropped.jpg" class="is-rounded" alt=""/>
            </figure>
          </b-navbar-item>
        </template>
        <template slot="start">
          <b-navbar-item>
            <font-awesome-icon icon="school"></font-awesome-icon>
            <p style="margin-left: 10px;">{{labels.Courses}}</p>
          </b-navbar-item>
          <b-navbar-item>
            <font-awesome-icon icon="book-open"></font-awesome-icon>
            <p style="margin-left: 10px;">{{labels.Library}}</p>
          </b-navbar-item>
        </template>
        <template slot="end">
          <b-navbar-item>
            <b-dropdown aria-role="list">
              <div slot="trigger">
                <font-awesome-icon icon="comments"></font-awesome-icon>
                <p style="margin-left: 10px;">{{labels.Messages}}</p>
              </div>

              <b-dropdown-item aria-role="listitem">
                oten
              </b-dropdown-item>
            </b-dropdown>
          </b-navbar-item>
          <b-navbar-item>
            <font-awesome-icon icon="bell"></font-awesome-icon>
            <p style="margin-left: 10px;">{{labels.Notifications}}</p>
          </b-navbar-item>
          <b-navbar-item @click="logout">
            <font-awesome-icon icon="sign-out-alt"></font-awesome-icon>
            <p style="margin-left: 10px;">{{labels.Logout}}</p>
          </b-navbar-item>
        </template>
      </b-navbar>
    </div>
  </header>
</template>

<script>
import bus from '@/main'

export default {
  name: "Navbar", 
  data() {
    return {
      navbarIsOpen: false,
      bus,
    }
  },

  computed: {
    labels() {
      if (this.navbarIsOpen) {
        return {
          Courses: "Courses",
          Library: "Library",
          Messages: "Messages",
          Notifications: "Notifications",
          Logout: "Logout"
        }
      } else {
        return {
          Courses: "",
          Library: "",
          Messages: "",
          Notifications: "",
          Logout: ""
        }
      }
    }
  },

  methods: {
    logout() {
      this.$buefy.dialog.confirm({
        message: "Are you sure you want to log out?",
        onConfirm: () => {bus.$emit("logout")}
      })
    }
  }
}

</script>

